/* eslint-disable react-hooks/exhaustive-deps */
import {
  Table,
} from "antd";
import React, {
  useEffect,
  useState,
} from "react";
import service from "../../../services/orders";
import { AntdMsg } from "../../../utils/Antd";

export default function OrderDetails({ type, orderId = null }) {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [qData, setQData] = useState({ page: 1, limit: 20, type, orderId });

  const columns = [
    {
      title: "Cat Id",
      dataIndex: "productVariantDetails",
      width: 250,
      render: (_) => _?.catId 
    },
    {
      title: "Product Name",
      dataIndex: "productDetails",
      width: 250,
      render: (_) => {
        return <span>{_?.name}</span>;
      },
    },
    {
      title: "Price",
      dataIndex: "productVariantDetails",
      width: 140,
      render: (_) => {
        return <span>₹{_?.price}</span>;
      },
    },
    {
      title: "With Discount Price",
      dataIndex: "productVariantDetails",
      width: 140,
      render: (_) => {
        return <span>₹{_?.withDiscountPrice || _?.price}</span>;
      },
    },
    // {
    //   title: "Quantity",
    //   dataIndex: "quantity",
    //   width: 50,
    // },

  ].filter((item) => !item.hidden);

  function list() {
    setLoading(true);
    service
      .orderDetailList(qData)
      .then((res) => {
        setData(res.data?.map((v) => ({ ...v, key: v._id })));
        setQData({
          ...qData,
          limit: res.extra.limit,
          page: res.extra.page,
          total: res.extra.total,
        });
      })
      .catch((err) => { })
      .finally(() => {
        setLoading(false);
      });
  }


  const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

  tableColumns[0].fixed = true;
  tableColumns[tableColumns.length - 1].fixed = "right";

  const tableProps = {
    bordered: true,
    loading,
    size: "small",
    tableLayout: undefined,
  };

  useEffect(() => {
    list();
  }, [qData.page, qData.limit]);
  return (
    <>
      <Table
        pagination={false}
        {...tableProps}
        columns={tableColumns}
        dataSource={data.length ? data : []}
      scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
      />
    </>
  );
}


